import React from "react";
import './navbar.css';
export default class Navbar extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      displayList : false
    }
    this.handleClick = this.handleClick.bind(this);
  }
  handleClick = ()=>{
      this.props.handleChange();
      this.setState(state=>({
        displayList : !state.displayList
      }))
  }
  render(){
    return(
    <header className={'fixed-top ' + (this.props.isMobile?" dropNav":"")}
    >   

        <div>

          <a href="#" className="text-decoration-none">
            <svg className="bi me-2" width="40" height="32"></svg>
            <span className={"heading"}>The SafeCure</span>
          </a>
          {this.props.isMobile && <button onClick={this.handleClick}>Switch</button>}
        </div>
        <ul className={!this.props.isMobile ?" desktopList ": " mobileList" + (!this.state.displayList?" hideThis":"")} >
            <li ><a href="#"  aria-current="page">Home</a></li>
            <li ><a href="#" >Services</a></li>
            <li ><a href="#" >Contact Us</a></li>
            <li ><a href="#" >About</a></li>
            <li ><button className="btn btn-primary"><a href="tel:8192061175" style={{color:"white",textDecoration:"none"}} >Call Us : 8192061175 </a></button></li>
            
        </ul>
        
    </header>
    )
  }
}